.blog1{
    width: 100%;
    /* height: 150%; */
    /* background: url('../../../Images/Abract01.png')no-repeat center center/cover; */
    /* position: absolute; */
  
 }


   .blog1 .container{
    display: grid;
    -ms-grid-columns: 70% 30%;
    grid-template-columns: 55% 39%;
   } 

   .blog1 .container .activities
     {
        font-size: 3rem;
        position: absolute;
        margin-left: 31rem;
        color: aliceblue;
        margin-top: 1%;
    }
   


   .container .videoBox { 
    width: 58%;
    margin-top: 5rem;
    /* margin-left: 0px; */
    
}   
.container .videoBox .video{
    filter: grayscale(1);
    border: 4px solid #09cce6;
    border-radius: 30px;
    display: block;
    /* height: 23rem; */
}

 .container .videoBox .video:hover {
    -webkit-filter: grayscale(0);
    filter: grayscale(0)
}

 .container .videoBox video {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -o-object-fit: cover;
    object-fit: cover;
    border-radius: 30px
}
.video-content span{
    font-size: 30px;
}

.video-content .text-18 {
    color: white;
    font-family: var(--Josefin);    
    font-size:  1.2rem;
}

.sidebar{
    display: grid;
    grid-template-columns: 45% 65%;
    margin-top: 4rem;
}

 .post-content> .post-image {
    width: 100%;
    /* -o-object-fit: cover;
    object-fit: cover; */
    border: 2px solid white ;
    /* margin: 2rem; */
    overflow: hidden;
}   
  

.post-content> .post-image div {
    display: initial;
} 

.post-content>.post-image .post-img {
    width: 100%;    
    height: 100%;
    -webkit-transition: all 1s ease;
    transition: all 1s ease;
    -o-object-fit: cover;
    object-fit: cover;
}

.post-content>.post-image .post-img:hover {
    -webkit-transform: scale(1.3);
    transform: scale(1.3)
}

 .post-content .post-title a {  
    font-family: var(--Anton);
    font-size: 1rem;
}

 .post-content .post-title p {
    color: var(--text-wite);
    font-size: .8rem;   
}

.category{
   
    margin-left: -12rem;
}
.category h2{
    margin-left: 1rem;
    color: white;

}
    

.category-list .btn {  
    font-family: var(--Anton);
    width: 20rem;
    font-size: 1.3rem;
    padding: 12px;
    background: var(--text-hover);
    border-radius: 24px;
    margin-top: 3rem;
    align-items: center;
    justify-content: center;
    display: flex;
    border: 2px solid white;
    cursor: pointer;
}

.category-list span {  
    font-family: var(--Anton);
    width: 20rem;
    font-size: 1.3rem;
    padding: 12px;
    background: var(--text-hover);
    border-radius: 24px;
    margin-top: 3rem;
    align-items: center;
    justify-content: center;
    display: flex;
    border: 2px solid white;
    cursor: pointer;
}

.sidebar .category .category-list button:hover{
background-color: #47dcff;


}       
     
.blog1 hr{
    margin-top: -43rem;
}

@media screen and (max-width:940px) {
    .category {
        /* margin-right: 6rem; */
        margin-left: 0rem;
    }
    .blog1 {
        width: 80%;
        /* height: 64%; */
        /* background: url(../../../Images/Abract01.png)no-repeat center center/cover; */
        /* position: absolute; */
        margin-bottom: 0%;
    }
    .blog1 .container {
        height: 56%;
        margin-left: 6rem;
        width: 100%;
    }
    .category-list .btn {
       
        width: 12rem;
    }

    .sidebar{
        grid-template-columns: auto;
    }
    /* .category-list .btn{
        padding: 3px;
    margin: 12px;
    } */

    .post-content{
        margin-right: 10rem;
        margin-left: -7rem;
    }
    .container .videoBox .video{
        height: 15rem;
    }

}

@media screen and (max-width:500px) {
    .blog1 {
        /* position: absolute; */
        width: 50%;
        height: 125%;
        
    }
    
    
   .blog1 .container{
        grid-template-columns: auto;
        /* height: 153vh; */
        /* align-items: center; */
        /* justify-items: center; */
        margin-left: 8rem;
        margin-top: 57%;
   } 
   .blog1 .container .activities{
    margin-left: -12%;
    width: max-content;
   }
   
   .container .videoBox{
    width: 100%; 
    
}

  .blog1 hr{
    display: none;
  }
  .category{
    width: 100%;
  }
  .post-content {
    width: 100%;
    margin-left: -5rem;
}
/* .category-list .btn { 
    width: 19rem;
} */

}
